
.experience-item{margin-bottom:20px
}
.experience-item span{color:rgba(74,74,74,0.75)
}
.experience-item p{color:rgba(74,74,74,0.75);margin-top:6px;transition:color 100ms ease-in-out
}
.experience-item:hover p{color:#4a4a4a
}
.experience-item.alt{margin-bottom:0
}
.experience-item.alt p{margin-top:0
}
.first{padding-top:15px !important
}
ul{list-style-type:none;margin:0;padding:0
}
li{display:inline-block
}
.is-3 strong{color:#e74c3c !important
}
.button{background:transparent !important;transition:all 200ms ease-in-out
}
.button:hover{border-color:rgba(231,76,60,0.75) !important;color:#e74c3c !important
}
a:not(.button){color:#e74c3c !important
}



